
.single-slider-selector[data-v-3fc0ded2] {
  width: 100%;
}
.labels[data-v-3fc0ded2] {
  position: relative;
}
.labels span[data-v-3fc0ded2] {
    font-size: 8px;
    -webkit-touch-callout: none;
    /* iOS Safari */
    -webkit-user-select: none;
    /* Safari */
    /* Konqueror HTML */
    -moz-user-select: none;
    /* Firefox */
    -ms-user-select: none;
    /* Internet Explorer/Edge */
    user-select: none;
    /* Non-prefixed version, currently
  supported by Chrome and Opera */
    width: 9.6%;
    display: inline-block;
}
.labels .one-hundred[data-v-3fc0ded2] {
    position: absolute;
    right: -10px;
    top: 3.5px;
}
input[type="range"][data-v-3fc0ded2]:active {
  cursor: ew-resize;
}
@media (max-width: 472px) {
input[type=range][data-v-3fc0ded2] {
    -webkit-appearance: none;
    margin: 18px 0;
    width: 100%;
}
input[type=range][data-v-3fc0ded2]:focus {
    outline: none;
}
input[type=range][data-v-3fc0ded2]::-webkit-slider-runnable-track {
    width: 100%;
    height: 8.4px;
    cursor: pointer;
    animate: 0.2s;
    background: lightgray;
    border-radius: 1.3px;
}
input[type=range][data-v-3fc0ded2]::-webkit-slider-thumb {
    border: 1px solid lightgray;
    height: 36px;
    width: 16px;
    border-radius: 3px;
    background: #ffffff;
    cursor: pointer;
    -webkit-appearance: none;
    margin-top: -14px;
}
input[type=range][data-v-3fc0ded2]::-moz-range-track {
    width: 100%;
    height: 8.4px;
    cursor: pointer;
    animate: 0.2s;
    background: lightgray;
    border-radius: 1.3px;
}
input[type=range][data-v-3fc0ded2]::-moz-range-thumb {
    border: 1px solid lightgray;
    height: 36px;
    width: 16px;
    border-radius: 3px;
    background: #ffffff;
    cursor: pointer;
}
}



